<template>
  <div class="mainXiaoGuo">
    <section class="head searchHead clearfix">
      <div class="backWrap">
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
      </div>
      <div class="searchHeadRight">
        <div class="chartWrap" @click="goCart">
          <span class="bag" v-if="indexData.shoppingCartNum > 0">
            <em class="font10"> {{ indexData.shoppingCartNum }} </em>
          </span>
          <a href="javascript:void(0)" class="iconfont cart"> &#xe669; </a>
        </div>
      </div>
      <div class="search search2" @click="$router.push('search')">
        <van-field
          v-model="query.searchValue"
          clearable
          left-icon="mysearch"
          placeholder="What are you looking for?"
        />
      </div>
    </section>

    <section class="operateRow">
      <div class="resultNum">{{ searchProductList.totalNum }} Results</div>
      <div class="operate">
        <span class="showStyle" @click="showStyle = showStyle == 1 ? 2 : 1">
          <em class="iconfont">
            {{ showStyle == 1 ? "&#xe688;" : "&#xe629;" }}
          </em>
        </span>
        <span class="sort" @click="sortVisible = true">
          <em
            class="iconfont"
            :class="{ red: sortValue == 1 || sortValue == 3 }"
          >
            &#xe674;
          </em>
          <em
            class="iconfont"
            :class="{ red: sortValue == 2 || sortValue == 4 }"
          >
            &#xe671;
          </em>
        </span>
        <span class="filter" @click="show = true">
          <em class="iconfont"> &#xe6e9; </em>
        </span>
      </div>
    </section>

    <section class="downListWrap">
      <div class="sortLayout" v-if="sortVisible">
        <ul>
          <li
            v-for="(item, index) in sortType"
            @click="changeSort(item.value)"
            :key="index"
            :class="{ active: item.value == sortValue }"
          >
            <a>{{ item.label }}</a>
          </li>
        </ul>
      </div>
      <div v-if="sortVisible" class="mask" @click="sortVisible = false"></div>

      <van-pull-refresh v-model="isLoading" @refresh="onRefresh" loosing-text='Release to refresh' loading-text='Loading...'>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="The end"
          @load="onLoad"
        >
          <div class="commonPro">
            <ul
              class="clearfix Prolist"
              v-if="showStyle == 2"
            >
              <li
                v-for="(item, index) in list"
                :key="index"
                @click="
                  $router.push({
                    path: '/proDetail',
                    query: { spuCode: item.spuCode }
                  })
                "
              >
                <div class="proBlock">
                  <div class="item">
                    <div class="tips_cutOff" v-if="item.percentage!='0%'" >
                      {{ item.percentage }} OFF
                    </div>
                    <div
                      class="tips_love iconfont"
                      :class="{ active: wishCode.indexOf(item.spuCode) > -1 }"
                      @click.stop="handlerLove(item)"
                    >
                      {{
                        wishCode.indexOf(item.spuCode) > -1
                          ? "&#xe653;"
                          : "&#xe652;"
                      }}
                    </div>
                    <div class="tips_sale" v-if="item.sale"></div>
                    <img class="img" :src="item.spuMainImage" alt="" />
                  </div>
                  <div class="name">
                    {{ item.name }}
                  </div>
                  <div class="bottomPrice">
                    <p class="red">{{ item.priceRange }} AED</p>
                    <p class="gray">{{ item.priceRangeLine }} AED</p>
                    <p v-if="item.isTypeFour == 1">Free Shipping</p>
                  </div>
                  <div class="downShare">
                    <div
                      :class="{ active: item.share }"
                      @click.stop="shareShow(item)"
                    >
                      <span class="share iconfont"> &#xe650; </span>
                    </div>
                    <div
                      :class="{ active: item.cart }"
                      @click.stop="addChart(item)"
                    >
                      <span class="cart iconfont"> &#xe669; </span>
                    </div>
                  </div>
                  <p class="others">{{ item.sumSku }} Other Items</p>
                </div>
              </li>
            </ul>

            <ul
              class="clearfix turnLine"
              v-if="showStyle == 1"
            >
              <li
                v-for="(item, index) in list"
                :key="index"
                @click="
                  $router.push({
                    path: '/proDetail',
                    query: { spuCode: item.spuCode }
                  })
                "
              >
                <div class="tips_cutOff" v-if="item.percentage!='0%'">
                  {{ item.percentage }} OFF
                </div>
                <div class="tips_sale" v-if="item.sale"></div>
                <div class="imgWrap">
                  <img class="img" :src="item.spuMainImage" />
                </div>
                <div class="rightMain">
                  <div class="name">{{ item.name }}</div>
                  <div class="downWrap">
                    <div class="bottomPrice">
                      <p class="red">
                        {{ item.priceRange }} AED
                        <span v-if="false" class="tips_vip"> VIP </span>
                      </p>
                      <p class="gray">{{ item.priceRangeLine }} AED</p>
                      <p v-if="item.isTypeFour == 1">Free Shipping</p>
                    </div>
                    <div class="downShare">
                      <p class="others">{{ item.sumSku }} Other Items</p>
                      <div
                        :class="{ active: item.share }"
                        @click.stop="shareShow(item)"
                      >
                        <span class="share iconfont"> &#xe650; </span>
                      </div>
                      <div
                        class="tips_love iconfont"
                        :class="{ active: wishCode.indexOf(item.spuCode) > -1 }"
                        @click.stop="handlerLove(item)"
                      >
                        {{
                          wishCode.indexOf(item.spuCode) > -1
                            ? "&#xe653;"
                            : "&#xe652;"
                        }}
                      </div>
                      <div
                        :class="{ active: item.cart }"
                        @click.stop="addChart(item)"
                      >
                        <span class="cart iconfont"> &#xe669; </span>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </van-list>
      </van-pull-refresh>
    </section>

    <van-popup v-model="show" position="right" style="height: 100%; width:80%;">
      <div class="filterLayoutWrap">
        <div class="filterLayout">
          <div class="blocks">
            <p class="tittle"><span> Filters</span></p>
            <div class="tags row">
              <div class="col">
                <a
                  href="javascript:void(0)"
                  :class="{ active: query.filterType != 2 }"
                  @click="changeFilter(1)"
                >
                  Only in Stock
                </a>
              </div>
              <div class="colM"></div>
              <div class="col">
                <a
                  href="javascript:void(0)"
                  :class="{ active: query.filterType == 2 }"
                  @click="changeFilter(2)"
                >
                  Free Shipping
                </a>
              </div>
            </div>
          </div>
          <div class="blocks mt10">
            <p class="tittle"><span>Price Range</span></p>
            <div class="range row">
              <div class="col">
                <input
                  type="number"
                  class="inputTxt"
                  v-model="priceRange[0]"
                  placeholder="Lowest Price"
                />
              </div>
              <div class="colM">
                —
              </div>
              <div class="col">
                <input
                  type="number"
                  class="inputTxt"
                  v-model="priceRange[1]"
                  placeholder="Highest Price"
                />
              </div>
            </div>
          </div>
          <div class="blocks radioBlocks mt10">
            <p class="tittle">
              <span>Categories</span>
              <span
                class="iconfont"
                @click="toggle.Categories = !toggle.Categories"
                :class="{ red: toggle.Categories }"
              >
                {{ toggle.Categories ? "&#xe674;" : "&#xe671;" }}
              </span>
            </p>
            <div v-show="toggle.Categories">
              <ul>
                <li
                  v-for="(item, index) in categoryOne"
                  :key="index"
                  :class="{ red: query.category1Code == item.categoryCode }"
                  @click="changeCategory(item)"
                >
                  <label>{{ item.categoryName }}</label>
                  <span class="iconfont">
                    {{
                      query.category1Code == item.categoryCode
                        ? "&#xe63a;"
                        : "&#xe608;"
                    }}
                  </span>
                </li>
              </ul>
            </div>
          </div>
          <div class="blocks radioBlocks mt10">
            <p class="tittle">
              <span>Brand</span>
              <span
                @click="toggle.Brand = !toggle.Brand"
                class="iconfont"
                :class="{ red: toggle.Brand }"
              >
                {{ toggle.Brand ? "&#xe674;" : "&#xe671;" }}
              </span>
            </p>
            <div v-show="toggle.Brand">
              <ul>
                <li
                  v-for="(item, index) in brands"
                  :class="{ red: query.brandId == item.id }"
                  @click="changeBrand(item.id)"
                  :key="index"
                >
                  <label>{{ item.brandName }}</label>
                  <span class="iconfont">
                    {{ query.brandId == item.id ? "&#xe63a;" : "&#xe608;" }}
                  </span>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="downBtnWrap">
          <van-button @click="resetForm" type="danger">Reset</van-button>
          <van-button
            @click="
              show = false;
              search();
            "
            type="danger"
            >Confirm</van-button
          >
        </div>
      </div>
    </van-popup>

    <add-cart ref="addCart"> </add-cart>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { getProListByName,brandList } from "@/api/product";
export default {
  name: "searchResult",
  data() {
    return {
      sortVisible: false,
      query: this.$route.query,
      searchProductList: {},
      list: [],
      loading: false,
      finished: false,
      pageNumber: 1,
      pageSize: 10,
      sortValue: 2,
      sortType: [
        { value: 1, label: "Price:Low to High" },
        { value: 2, label: "Price:High to Low" },
        { value: 3, label: "Best Sale" },
        { value: 4, label: "High Rated" }
      ],
      showStyle: 1,
      show: false,
      toggle: {
        Categories: 1,
        Brand: 0
      },
      brands:[],
      priceRange: ["", ""],
      radioValue: -1,
      radioValue2: -1,
      isLoading: true
    };
  },
  computed: {
    ...mapGetters(["indexData", "categoryOne", "wishCode", "wishes"])
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      brandList().then(res=>{
        this.brands=res.data.result.result;
      });
      this.search();
    },
    goCart() {
      this.$router.push({
        path: "/cart",
        query: {}
      });
    },
    onLoad() {
       if (this.isLoading) {
        this.loading = false;
        return false;
      }
      this.query.pageNo++;
      let query = this.query;
      getProListByName({
        name: query.searchValue,
        orderByVolume: query.orderByVolume,
        orderByPrice: query.orderByPrice,
        category2Code: query.category2Code,
        category1Code: query.category1Code,
        filterType: query.filterType || "1",
        brandId: query.brandId,
        pageNo: query.pageNumber,
        pageSize: query.pageSize,
        smallPrice: this.priceRange[0] || null,
        largePrice: this.priceRange[1] || null
      })
        .then(res => {
          this.list=this.list.concat(res.data.result.result);
           this.loading = false;
          if (res.data.result.totalNum <= this.list.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.loading=false;
        });
    },
    onRefresh() {
      this.search();
    },
    search() {
      this.query = Object.assign(this.query, { pageNo: 1, pageSize: 10 });
      let query = this.query;
      getProListByName({
        name: query.searchValue,
        orderByVolume: query.orderByVolume,
        orderByPrice: query.orderByPrice,
        category2Code: query.category2Code,
        category1Code: query.category1Code,
        filterType: query.filterType || "1",
        brandId: query.brandId,
        pageNo: query.pageNo,
        pageSize: query.pageSize,
        smallPrice: this.priceRange[0] || null,
        largePrice: this.priceRange[1] || null
      })
        .then(res => {
          this.searchProductList = res.data.result;
          this.list = this.searchProductList.result;
          this.isLoading = false;
          if (res.data.result.totalNum <= this.list.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.isLoading = false;
        });
    },
    changeSort(val) {
      this.query.orderByPrice = "";
      this.query.orderByVolume = "";
      if (val < 3) {
        this.query.orderByPrice = val == 1 ? "ASC" : "DESC";
      } else if (val == 3) {
        this.query.orderByVolume = "DESC";
      }
      this.sortValue = val;
      this.sortVisible = false;

      this.search();
    },
    goBack() {
      this.$router.back(-1);
    },
    changeFilter(val) {
      this.$set(this.query, "filterType", val);
    },
    changeCategory(item) {
      this.$set(this.query, "category1Code", item.categoryCode);
    },
    changeBrand(id) {
      this.$set(this.query, "brandId", id);
    },
    addChart(data) {
      this.$refs.addCart.load(data);
    },
    handlerLove(item) {
      let index = this.wishCode.indexOf(item.spuCode);
      let isAdd = index == -1;
      this.$store
        .dispatch(
          isAdd ? "addWish" : "deleteWish",
          isAdd
            ? { type: "spu", code: item.spuCode }
            : { id: this.wishes.result[index].id }
        )
        .then(res => {
          if(isAdd){
            this.$toast("Move to Whislist Successfully");
          }else{
            this.$toast("Remove From Whislist Successfully");
          }
          this.$store.dispatch("myWishes");
        });
    },
    resetForm() {
      this.show = false;
      this.priceRange = ["", ""];
      this.$set(this.query, "brandId", "");
      this.$set(this.query, "category1Code", "");
    },
    shareShow(item) {
      this.$store
        .dispatch("share", {
          imageUrl: [item.spuMainImage],
          url: "proDetail?spuCode="+item.spuCode,
          title: item.name,
        })
        .then(res => {});
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/search.scss";
</style>



